import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";

const Container = ({ children, className, style }) => {
  return (
    <div className={`container ${className || ""}`} style={style}>
      {children}
    </div>
  );
};

const ContainerFluid = ({ children, className, style }) => {
  return (
    <div className={`container-fluid ${className || ""}`} style={style}>
      {children}
    </div>
  );
};

const Row = ({ children, className }) => {
  return <div className={`row ${className || ""}`}>{children}</div>;
};

const Col = ({ size, children, className }) => {
  return <div className={`col-${size} ${className || ""}`}>{children}</div>;
};

Container.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node.isRequired,
};

Row.propTypes = {
  className: PropTypes.string,
  children: PropTypes.node.isRequired,
};

Col.propTypes = {
  className: PropTypes.string,
  size: PropTypes.number.isRequired,
  children: PropTypes.node.isRequired,
};

const CirclePhotoContainer = styled.div`
  height: ${(props) => props.size}px;
  width: ${(props) => props.size}px;
  border-radius: 50%;
  overflow: hidden;
  margin-top: ${(props) => (props.marginTop ? `${props.marginTop}px` : "0")};
  margin-bottom: ${(props) =>
    props.marginBottom ? `${props.marginBottom}px` : "0"};
`;

const CirclePhotoImg = styled.img`
  height: 100%;
  width: 100%;
  object-fit: cover;
`;

function CirclePhoto({ src, alt, size, marginTop, marginBottom }) {
  return (
    <CirclePhotoContainer
      size={size}
      marginTop={marginTop}
      marginBottom={marginBottom}>
      <CirclePhotoImg src={src} alt={alt} />
    </CirclePhotoContainer>
  );
}

CirclePhoto.propTypes = {
  src: PropTypes.string.isRequired,
  alt: PropTypes.string.isRequired,
  size: PropTypes.number.isRequired,
  marginTop: PropTypes.number,
  marginBottom: PropTypes.number,
};

export { CirclePhoto, Container, ContainerFluid, Row, Col };
