// import React, { useState } from "react";
import { Link } from "react-router-dom";
import "../css/Navbar.css";

const menuItems = [
  {
    name: "Solutions",
    route: "/solutions",
    subItems: [
      { name: "Hercules", route: "/solutions/hercules" },
      { name: "Emergence Engine", route: "/solutions/emergence-engine" },
      { name: "Datavis", route: "/solutions/datavis" },
    ],
  },
  // {
  //   name: "Technology",
  //   route: "/technology",
  //   subItems: [
  //     { name: "Overview", route: "/technology/overview" },
  //     { name: "Security", route: "/technology/security" },
  //   ],
  // },
  {
    name: "Resources/Blog",
    route: "/resources",
  },
  {
    name: "About Us",
    route: "/about",
    subItems: [
      { name: "Contact Us", route: "/contact" },
      // { name: "Mission/Values", route: "/about/mission" },
      { name: "Team Members", route: "/about/team" },
    ],
  },
];

function Navbar() {
  const renderMenuItems = (items) => {
    return items.map((item, index) => {
      if (item.subItems) {
        return (
          <li key={index} className="nav-item dropdown">
            <a
              className="nav-link dropdown-toggle"
              // href={item.route}
              href="#"
              id={`navbarDropdown${index}`}
              role="button"
              data-bs-toggle="dropdown"
              aria-expanded="false">
              {item.name}
            </a>
            <ul
              className="dropdown-menu"
              aria-labelledby={`navbarDropdown${index}`}>
              {renderMenuItems(item.subItems)}
            </ul>
          </li>
        );
      } else {
        return (
          <li key={index} className="nav-item">
            <Link to={item.route} className="nav-link">
              {item.name}
            </Link>
          </li>
        );
      }
    });
  };

  return (
    <nav className="navbar navbar-expand-lg navbar-light bg-light">
      <div className="container">
        <Link to="/" className="navbar-brand">
          <img
            src={process.env.PUBLIC_URL + "/img/logo.svg"}
            alt="Your Logo"
            className="site-logo"
          />
        </Link>

        <button
          className="navbar-toggler"
          type="button"
          data-bs-toggle="collapse"
          data-bs-target="#navbarSupportedContent"
          aria-controls="navbarSupportedContent"
          aria-expanded="false"
          aria-label="Toggle navigation">
          <span className="navbar-toggler-icon"></span>
        </button>

        <div className="navbar-collapse collapse" id="navbarSupportedContent">
          <ul className="navbar-nav mb-2 mb-lg-0">
            {renderMenuItems(menuItems)}
          </ul>
        </div>
      </div>
    </nav>
  );
}

export default Navbar;
