import React, { useState } from "react";
import { Toast } from "react-bootstrap";
import { Col, Row } from "./../components/Layouts";

const ContactUsContactForm = () => {
  const [email, setEmail] = useState("");
  const [message, setMessage] = useState("");
  const [emailError, setEmailError] = useState("");
  const [messageError, setMessageError] = useState("");
  const [showToast, setShowToast] = useState(false);

  const handleSubmit = (event) => {
    event.preventDefault();

    let isValid = true;

    if (email.trim() === "") {
      isValid = false;
      setEmailError("Please enter your email address");
    } else if (!isValidEmail(email)) {
      isValid = false;
      setEmailError("Please enter a valid email address");
    } else {
      setEmailError("");
    }

    if (message.trim() === "") {
      isValid = false;
      setMessageError("Please enter your message");
    } else {
      setMessageError("");
    }

    if (isValid) {
      // Submit the form
      const formData = new FormData();
      formData.append("email", email);
      formData.append("message", message);
      setShowToast(true);

      fetch("https://formspree.io/f/xnqkqvoa", {
        method: "POST",
        body: formData,
      })
        .then((response) => response.json())
        .then((data) => {
          // Handle form submission success
          console.log("Form submitted successfully:", data);
        })
        .catch((error) => {
          // Handle form submission error
          console.error("Form submission error:", error);
        });
    }
  };

  const isValidEmail = (email) => {
    const emailRegex = /^[^\s@]+@[^\s@]+\.[^\s@]+$/;
    return emailRegex.test(email);
  };

  return (
    <Row>
      <Col size={8}>
        <div className="contact-form d-flex justify-content-center">
          <form
            onSubmit={handleSubmit}
            className="form-grid d-flex flex-column align-items-start">
            <div className="form-group">
              <label className="form-label">Your email:</label>
              <input
                type="email"
                name="email"
                value={email}
                onChange={(event) => setEmail(event.target.value)}
                className="form-input"
              />
              <div className="error-msg">{emailError}</div>
            </div>

            <div className="form-group">
              <label className="form-label">Your message:</label>
              <textarea
                name="message"
                value={message}
                onChange={(event) => setMessage(event.target.value)}
                className="form-textarea"></textarea>
              <div className="error-msg">{messageError}</div>
            </div>

            <button type="submit" className="form-btn ml-auto">
              Send
            </button>
          </form>

          <Toast
            show={showToast}
            onClose={() => setShowToast(false)}
            delay={5000}
            autohide>
            <Toast.Header>
              <strong className="mr-auto">Thank you!</strong>
            </Toast.Header>
            <Toast.Body>
              Thank you for submitting your message. Our team will be in touch
              with you soon!
            </Toast.Body>
          </Toast>
        </div>
      </Col>
    </Row>
  );
};

export default ContactUsContactForm;
